body{
    font-family: 'Raleway', sans-serif;
}

body.dark-theme {
    background-color: #90B8F8;
}

.previewBox{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow-y: scroll;
    position: fixed;
    z-index: 200;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 100px 0;
}

.close-previewBox{
    border: none;
    cursor: pointer;
    margin-left: auto;
    margin-top: 10px;
    margin-bottom: 10px;
}
